<template>
  <div @click="languageHandle">
    <el-tooltip effect="dark" :content="$t('hint.language')" placement="bottom-start">
      <svg t="1541055219442" class="icon" style="" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="1972" xmlns:xlink="http://www.w3.org/1999/xlink" width="18" height="18">
        <path d="M651.300262 390.041088h325.223765a47.760133 47.760133 0 0 1 46.622987 48.89728v536.164353a47.760133 47.760133 0 0 1-46.622987 48.897279h-557.201555a47.760133 47.760133 0 0 1-46.622987-48.897279v-341.143809H46.907147a47.760133 47.760133 0 0 1-46.622987-48.89728V48.897279A47.760133 47.760133 0 0 1 46.907147 0h557.770128a47.760133 47.760133 0 0 1 46.622987 48.897279zM46.907147 585.061632h557.770128V48.897279H46.907147v536.164353zM682.571778 568.573015l-102.343143 278.600777h45.485841l25.017213-71.6402h113.714603l25.017213 71.6402h43.780122L730.331911 568.573015z m-22.174348 170.571904l44.348696-126.223209 47.19156 126.223209zM306.176442 113.714603H341.427969v71.071627h135.320378v188.197668h-35.251527v-22.174348H341.427969v130.771794h-33.545808v-130.771794H210.656176v22.174348h-35.251527V184.78623h132.477512zM210.656176 314.420877h96.657412V221.174903H210.656176z m130.771793 0h100.068851V221.174903H341.427969z" fill="#000000" p-id="1973"/>
      </svg>
    </el-tooltip>
  </div>
</template>

<script>
export default {
  name: 'language',
  methods: {
    languageHandle () {
      let language = this.$store.getters.language === 'en' ? 'zh' : 'en'
      this.$i18n.locale = language
      this.$store.dispatch('setLanguage', language)
      this.$message({
        message: this.$t('message.switchLanguage'),
        type: 'success'
      })
    }
  }
}
</script>



